import React from 'react'
import styled from 'styled-components'
import { LiveProvider, LiveEditor, LiveError, LivePreview } from 'react-live'

const Left = styled.div`
  float: left;
  width: 50%;
`
const Right = styled.div`
  float: right;
  width: 50%;
`
const Clear = styled.div`clear: both;`

const Center = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
`

const Container = styled.div`
  width: 80%;
  max-width: 800px;
  margin: 50px auto;
`

/*
  Remove the first line as imports do not work in the
  live environment
*/

const removeFirstLine = code => {
  code = code.trim().split('\n')
  code.splice(0, 1)
  return code.join('\n')
}

const Playground = props =>
  <Container>
    <LiveProvider
      code={props.code}
      scope={props.scope}
      transformCode={removeFirstLine}
      style={{ borderRadius: '2px' }}
    >
      <Left>
        <LiveEditor style={{ minHeight: '100px' }} />
      </Left>
      <Right>
        <Center style={{ minHeight: '100px' }}>
          <LiveError />
          <LivePreview />
        </Center>
      </Right>
      <Clear />
    </LiveProvider>
  </Container>

export default Playground



