import React from 'react'
import Playground from '../common/playground'

import Button from '../components/button'
const scope = { Button }

/*

  The first line in this block is not compiled
  in the playground.

  It's exists so that folks can copy paste it into their
  editors.

*/
const simpleExample = `
  import Button from 'fabric/button'

  <Button>BUTTON</Button>
`

const primaryExample = `
  import Button from 'fabric/button'

  <Button primary>BUTTON</Button>
`

const transparentExample = `
  import Button from 'fabric/button'

  <Button transparent>BUTTON</Button>
`

const ButtonPage = () =>
  <div>
    <Playground code={simpleExample} scope={scope} />
    <Playground code={primaryExample} scope={scope} />
    <Playground code={transparentExample} scope={scope} />
  </div>

export default ButtonPage



